<template>
  <div class="album">
    <van-loading
      class="loading"
      text-color="#999"
      v-if="albumList == ''"
      color="red"
      type="spinner"
      size="24px"
      >正在加载...</van-loading
    >
    <van-list
      v-if="albumList != ''"
      :immediate-check="false"
      v-model="loading"
      :finished="finished"
      finished-text="查看更多请前往网易云APP~"
      @load="onLoad"
    >
      <div v-for="item in albumList" :key="item.id" class="ablum-item">
        <div class="img">
          <img :src="item.picUrl+'?param=100y100'" />
        </div>
        <div class="ablum-text">
          <p class="name">{{ item.name }}</p>
          <p class="info">
            {{ item.artist }} &nbsp;
            {{ item.publishTime | getTime("YYYY.MM.DD") }}
          </p>
        </div>
      </div>
    </van-list>
  </div>
</template>

<script>
import { getSearchResult } from "../../../NetRequest/search";
export default {
  data() {
    return {
      albumList: [],
      key: "",
      count: 15,
      loading: false,
      finished: false,
      index: 0,
      onLoadCount: 0,
      hasMore: true,
    };
  },
  mounted() {
    this.key = this.$route.query.key;
    setTimeout(() => {
      this.getAlbumList();
    }, 1000);
  },
  methods: {
    getAlbumList() {
      if (!this.hasMore) {
        this.finished = true;
        return;
      }
      getSearchResult(this.key, this.count, this.index, 10).then((res) => {
        // console.log(res);
        // this.albumList = [];
        this.hasMore = res.data.result.hasMore;
        const result = res.data.result.albums;
        // if (result.length < this.count + this.index) {
          this.pushData(result);
        //   this.finished = true;
        // } else {
          this.index += 10;
        //   this.pushData(result);
        // }
        this.loading = false;
      });
    },
    pushData(result) {
      for (const item of result) {
        this.albumList.push({
          id: item.id,
          name: item.name,
          picUrl: item.picUrl,
          artist: item.artist.name,
          publishTime: item.publishTime,
        });
      }
    },
    onLoad() {
      if (++this.onLoadCount >= 9) {
        this.finished = true;
        return;
      }
      setTimeout(() => {
        this.getAlbumList();
      }, 2000);
    },
  },
};
</script>

<style scoped>
.album {
  margin-top: 110px;
  margin-bottom: 50px;
}
p {
  height: 20px;
  margin: 5px;
  max-width: 220px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.loading {
  margin-top: 150px;
  margin-left: 40%;
}
.ablum-item {
  display: flex;
  justify-content: left;
  margin-bottom: 15px;
}
.ablum-item .img img {
  width: 60px;
  height: 60px;
  border-radius: 10px;
}
.ablum-text .info {
  font-size: 13px;
  color: rgb(149, 149, 149);
}
</style>