<template>
  <div class="comprehensive">
    <van-loading
      class="loading"
      text-color="#999"
      v-if="songList == ''"
      color="red"
      type="spinner"
      size="24px"
      >正在加载...</van-loading
    >
    <div v-if="songList != ''">
      <!-- 单曲 -->
      <el-card class="box-card first-card">
        <div slot="header">
          <span class="title">单曲</span>
          <div class="playBtn" style="float: right; padding: 3px 10px">
            <span class="iconfont icon-caret_right"></span>播放
          </div>
        </div>
        <div v-for="(item,index) in songList" :key="item.id" class="song">
          <div class="item">
            <div  @click="playThis(item.id,index)" class="text">
              <p class="name">{{ item.name }}</p>
              <p class="singer">{{ item.artists }}</p>
            </div>
            <div class="icon">
              <span class="iconfont icon-bofang"></span>
              <span class="iconfont icon-liebiao-dian"></span>
            </div>
          </div>
          <div class="divider"></div>
        </div>
        <div class="moreText">
          <span>{{ songCount }}></span>
        </div>
      </el-card>
      <!-- 歌单 -->
      <el-card class="box-card">
        <div slot="header">
          <span class="title">歌单</span>
        </div>
        <div class="gedan">
          <div  @click="toPlayListDetail(item.id)" v-for="item in gedanList" :key="item.id" class="gedan-item">
            <div class="img">
              <img :src="item.coverImgUrl+'?param=100y100'" />
            </div>
            <div class="gedan-text">
              <p class="name">{{ item.name }}</p>
              <p class="info">
                {{ item.trackCount }}首，by{{ item.creator }}，播放{{
                  item.playCount
                }}次
              </p>
              <p class="clickPercentage" v-if="item.officialTags != ''">
                {{ item.officialTags }}
              </p>
            </div>
          </div>
        </div>
        <div class="divider"></div>
        <div class="moreText">
          <span>{{ gedanCount }}></span>
        </div>
      </el-card>
      <!-- 专辑 -->
      <el-card class="box-card" v-if="this.albumList != ''">
        <div slot="header">
          <span class="title">专辑</span>
        </div>
        <div class="ablum">
          <div v-for="item in albumList" :key="item.id" class="ablum-item">
            <div class="img">
              <img :src="item.picUrl+'?param=100y100'" />
            </div>
            <div class="ablum-text">
              <p class="name">{{ item.name }}</p>
              <p class="info">
                {{ item.artist }} &nbsp;
                {{ item.publishTime | getTime("YYYY.MM.DD") }}
              </p>
            </div>
          </div>
        </div>
        <div class="divider"></div>
        <div class="moreText">
          <span>{{ gedanCount }}></span>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
import { getSearchResult } from "../../../NetRequest/search";
import { toStringNum, findItem } from "../../../common/common.js";
export default {
  data() {
    return {
      songList: [],
      songCount: "",
      gedanList: [],
      gedanCount: "",
      albumList: [],
      albumCount: "",
      hasClcikMusicId:''
    };
  },
  mounted() {
    // this.$bus.$on('getSearchData',this.getSearchData)
    // console.log(this.$route.query);
    setTimeout(() => {
      this.getSearchData();
    }, 1000);
  },
  methods: {
    getSearchData() {
      const key = this.$route.query.key;
      // console.log(key);
      //   console.log("!!!");
      getSearchResult(key, "", "", 1018).then((res) => {
        // console.log(res);
        /* 单曲 */
        for (const item of res.data.result.song.songs) {
          this.songList.push({
            id: item.id,
            name: item.name,
            artists: item.al.name,
            picUrl:item.al.picUrl
          });
        }
        if(res.data.result.song.moreText){
          this.songCount = res.data.result.song.moreText;
        }else{
          this.songCount = '没有更多了'
        }
        
        /* 歌单 */
        for (const item of res.data.result.playList.playLists) {
          this.gedanList.push({
            id: item.id,
            name: item.name,
            trackCount: item.trackCount,
            creator: item.creator.nickname,
            playCount: toStringNum(item.playCount),
            officialTags: item.officialTags[0],
            coverImgUrl: item.coverImgUrl,
          });
        }
        if(res.data.result.song.moreText){
        this.gedanCount = res.data.result.playList.moreText;
        }else{
          this.songCount = '没有更多了'
        }
        /* 专辑 */
        if (res.data.result.album) {
          for (const item of res.data.result.album.albums) {
            this.albumList.push({
              id: item.id,
              name: item.name,
              picUrl: item.picUrl,
              artist: item.artist.name,
              publishTime: item.publishTime,
            });
          }
        if(res.data.result.song.moreText){
          this.albumCount = res.data.result.album.moreText;
        }else{
          this.songCount = '没有更多了'
        }
        }else{
          this.albumList = ''
        }

        
      });
    },
    /* 点击播放歌曲 */
    playThis(musicId,i){
      // console.log(musicId,i);
      this.$store.state.playShow = true
      const index = findItem(this.$store.state.playList,'id',musicId)
      if(index != -1){//如果播放列表有这首歌
        if(this.hasClcikMusicId == musicId){
          //如果被点击这首歌已经被点击过且正在播放
          return
        }
        this.$bus.$emit('receiveMusicId',musicId,index)
      } else{//播放列表没有这首歌
        const newIndex = this.$store.state.playList.length
        this.$store.state.playList.push(this.songList[i])
        // console.log(this.$store.state.playList);
        this.$bus.$emit('receiveMusicId',musicId,newIndex)
        this.hasClcikMusicId = musicId
      }
    },
    /* 歌单详情 */
    toPlayListDetail(id) {
      // console.log(id);
      this.$router.push({
        path: "/recommendMore/playListDetail",
        query: {
          id,
        },
      });
    },
  },
  watch: {
    // $route: "getSearchData",
  },

  beforeDestroy() {
    sessionStorage.clear("activePage");
    // console.log(sessionStorage.getItem("activePage"));
  },
};
</script>

<style scoped>
.comprehensive{
  margin-bottom: 50px;
}
.box-card {
  margin-top: 20px;
}
.loading {
  margin-top: 150px;
  margin-left: 40%;
}
.first-card {
  margin-top: 110px;
}
.title {
  font-weight: 800;
}
.playBtn {
  /* width: 73px;height: 32px; */
  color: #333;
  border-radius: 20px;
  border: #ccc 1px solid;
}
.song .item {
  width: 100%;
  display: flex;
  justify-content: space-between;
  height: 60px;
}
p {
  height: 20px;
  margin: 5px;
  max-width: 220px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.singer {
  font-size: 13px;
  color: rgb(149, 149, 149);
}
.icon {
  line-height: 60px;
  text-align: center;
}
.icon .iconfont:nth-child(1) {
  margin-right: 15px;
}
.icon .iconfont {
  font-size: 20px;
}
.divider {
  width: 100%;
  height: 1px;
  border-top: solid 1px #ccc;
}
.moreText {
  /* line-height: 50px; */
  text-align: center;
  margin-top: 10px;
  font-size: 13px;
  color: rgb(149, 149, 149);
}
.gedan-item,
.ablum-item {
  display: flex;
  justify-content: left;
  margin-bottom: 15px;
}
.gedan-item .img img,
.ablum-item .img img {
  width: 60px;
  height: 60px;
  border-radius: 10px;
}
.gedan-text .info,
.ablum-text .info {
  font-size: 12px;
  color: rgb(149, 149, 149);
}
.clickPercentage {
  font-size: 12px;
  color: rgb(255, 200, 100);
}
</style>