import { request } from "./request";

//轮播图
export function getBanner(type){
    return request ({
        url: '/banner',
        params: {
            type
        }
    })
}

//推荐歌单
export function getRecommend(limit){
    return request ({
        url: '/personalized',
        params: {
            limit
        }
    })
}

//新歌速递
export function getNewSongs(type){
    return request({
        url: '/top/song',
        params: {
            type
        }
    })
}

//最新MV
export function getMV(area,limit){
    return request({
        url: '/mv/first',
        params: {
            area,
            limit
        }
    })
}

//热门歌手
export function getHotSinger(limit,offset){
    return request({
        url: '/top/artists',
        params: {
            limit,
            offset
        }
    })
}