<template>
  <div class="contain">
    <div class="content"  v-if="hotCommentsList!=''">
      <div class="header">
        <div class="navBar">
          <i @click="backMusic" class="iconfont icon-arrowLeft"></i>
          <span>评论({{ commentsTotle }})</span>
          <i class="iconfont icon-fenxiang"></i>
        </div>
        <div class="title">
          <div class="discContainer">
            <img class="disc" src="../../../assets/image/disc.png" alt="" />
            <img class="musicImg" :src="$store.state.musicInfo[0].picUrl" />
          </div>
          <span>{{ $store.state.musicInfo[0].name }}-{{ $store.state.musicInfo[0].artists }}-</span>
        </div>
        <div class="divider"></div>
        <div class="nva">
          <span>评论区</span>
          <div class="tab">
            <van-tabs
              v-model="active"
              title-active-color="#333"
              title-inactive-color="#999"
              line-width="0px"
              line-height="0px"
            >
              <van-tab title="推荐" name="recommend"></van-tab>
              <van-tab title="最热" name="hot"></van-tab>
              <van-tab title="最新" name="new"></van-tab>
            </van-tabs>
          </div>
        </div>
      </div>
      <van-loading
        class="loading"
        text-color="#999"
        v-if="!isShow"
        color="red"
        type="spinner"
        size="24px"
        >正在加载...</van-loading
      >
      <div v-if="isShow" class="main">
        <div class="item" v-for="(item, index) in hotCommentsList" :key="index">
          <div class="itemHead">
            <div class="left">
              <img class="userImg" :src="item.picUrl" />
              <div class="text">
                <span class="username">{{ item.name }}</span>
                <br />
                <span class="sendTime">{{
                  item.publishTime | getTime("YYYY.MM.DD")
                }}</span>
              </div>
            </div>
            <div class="right">
              <span>{{ item.likedCount }}</span
              >&nbsp;
              <i class="iconfont icon-zan"></i>
            </div>
          </div>
          <div class="comment">
            <p>{{ item.content }}</p>
          </div>
          <van-divider />
        </div>
        <div class="end">查看更多请前往网易云APP~</div>
      </div>
      <div class="footer">
        <!-- <input class="input" type="text" placeholder="发评论带#喜提热评#，更容易上热评哦~"> -->
        <textarea
          class="input"
          clos="20"
          rows=""
          warp="virtual"
          placeholder="发评论带#喜提热评#，更容易上热评哦~"
        ></textarea>
        <div class="send">
          <img src="../../../assets/image/tie.jpg" alt="" />
          <img src="../../../assets/image/niu.jpg" alt="" />
          <span>发送</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getMusicComments } from "../../../NetRequest/play";
export default {
  data() {
    return {
      active: "recommend",
      commentsList: [],
      musicId: "",
      hotCommentsList: [],
      commentsTotle: "",
      defaultShowComments: [],
      isShow: false,
    };
  },
  created() {
    this.musicId = this.$store.state.playingMusicId;
  },
  mounted() {
    this.$store.state.updataComment = true
    this.getComments();
    setTimeout(()=>{
      this.isShow = true
    },1000)
  },
  methods: {
    backMusic() {
      // this.$store.commit("showComment")
      this.$store.state.showComment = false;
    },
    /* 歌曲评论 */
    async getComments() {
      this.commentsTotle = "";
      this.commentsList = [];
      this.hotCommentsList = [];
      const res = await getMusicComments(this.musicId, 30, this.index);
      // console.log(res);
      this.commentsTotle = res.data.total;
      for (const item of res.data.comments) {
        this.commentsList.push({
          id: item.user.userId,
          name: item.user.nickname,
          picUrl: item.user.avatarUrl,
          likedCount: item.likedCount,
          publishTime: item.time,
          content: item.content,
        });
      }
      for (const item of res.data.hotComments) {
        this.hotCommentsList.push({
          id: item.user.userId,
          name: item.user.nickname,
          picUrl: item.user.avatarUrl,
          likedCount: item.likedCount,
          publishTime: item.time,
          content: item.content,
        });
      }
      // console.log(this.commentsTotle);
      // console.log(this.commentsList);
      // console.log(this.hotCommentsList);
    },
    updata(id) {
      this.musicId = id;
      // console.log(id,info);
      this.getComments();
      // setTimeout(()=>{
      // },1000)
    },
  },
  // computed: {
  //   musicId() {
  //     return this.$store.state.playingMusicId;
  //   },
  // },
  // watch: {
  //   musicId() {
  //     // console.log("musicId改变了");
  //     this.getComments()
  //   },
  //   immediate:true,
  // },
};
</script>

<style scoped>
.content {
  position: relative;
  z-index: 100;
}
.loading {
  margin-top: 150px;
  margin-left: 40%;
}
.header {
  position: fixed;
  top: 0;
  width: 100%;
  /* padding: 15px; */
  background-color: #fff;
}
.main {
  margin-top: 170px;
  padding: 5px 15px 70px 15px;
  background-color: #fff;
}
.navBar {
  display: flex;
  justify-content: space-between;
  padding: 0 15px;
  line-height: 50px;
  text-align: center;
}
.navBar .iconfont {
  font-size: 30px;
}
.navBar span {
  position: relative;
  left: -80px;
}
.title {
  display: flex;
  justify-content: left;
  padding: 0 15px;
  /* height: 70px;
    text-align: center; */
}
.title span {
  /* margin-left: -30px; */
  line-height: 70px;
  text-align: left;
  max-width: 60%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.discContainer {
  position: relative;
  width: 70px;
  height: 70px;
  background-color: #fff;
}
.disc {
  width: 70px;
  height: 70px;
}
.musicImg {
  width: 43px;
  height: 43px;
  border-radius: 50%;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
}
.divider {
  position: relative;
  width: 100%;
  height: 10px;
  background-color: rgb(247, 247, 247);
}
.nva {
  display: flex;
  justify-content: space-between;
  padding: 0 15px;
}
.nva span {
  line-height: 44px;
  text-align: center;
}
.userImg {
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.itemHead {
  display: flex;
  justify-content: space-between;
}
.left {
  max-width: 70%;
  display: flex;
}
.left .text {
  margin: auto;
  margin-left: 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.text .sendTime {
  font-size: 10px;
  color: #999;
}
.right {
  margin: auto;
  color: #999;
  margin-right: 0;
}
.comment {
  margin-left: 60px;
}
.end {
  height: 30px;
  line-height: 30px;
  text-align: center;
}
.footer {
  position: fixed;
  bottom: -1px;
  display: flex;
  width: 100%;
  padding: 0 15px;
  height: 60px;
  background-color: #fff;
  border-top: #999 0.1px solid;
}
.input {
  width: 60%;
  height: 60px;
  border: none;
  outline: medium;
  font-size: 14px;
}
.send img {
  position: relative;
  top: 10px;
  width: 30px;
  height: 30px;
  padding-right: 5px;
}
.send span {
  margin-left: 10px;
  line-height: 60px;
  text-align: center;
  font-size: 14px;
  color: rgb(222, 222, 222);
}
</style>