<template>
  <div class="videoDetail">
    <van-loading
      class="loading"
      v-if="videoList == ''"
      text-color="#999"
      color="red"
      type="spinner"
      size="24px"
      >正在加载...</van-loading
    >
    <van-list
      v-if="videoList != ''"
      :immediate-check="false"
      v-model="loading"
      :finished="finished"
      finished-text="查看更多请前往网易云APP~"
      @load="onLoad"
    >
      <div class="video">
        <div class="video-item" v-for="item in videoList" :key="item.id">
          <div class="user">
            <img :src="item.avatarUrl+'?param=100y100'" />
            <p>{{ item.username }}</p>
          </div>
          <div class="title">
            <p>{{ item.title }}</p>
          </div>
          <div class="img">
            <img :src="item.coverUrl+'?param=500y500'" alt="" />
          </div>
        </div>
      </div>
    </van-list>
  </div>
</template>

<script>
import { getSearchResult } from "../../../NetRequest/search";
import { getUserDetail } from "../../../NetRequest/user";
export default {
  data() {
    return {
      videoList: [],
      uid: [],
      avatar: [],
      key: "",
      count: 5,
      loading: false,
      finished: false,
      index: 0,
      onLoadCount: 0,
      hasMore: true,
    };
  },
  mounted() {
    this.key = this.$route.query.key;
    // this.getVideoList();
    setTimeout(() => {
      // console.log("@@@");
      this.getVideoList();
    }, 1000);
  },
  methods: {
    getVideoList() {
      if (!this.hasMore) {
        this.finished = true;
        return;
      }
      getSearchResult(this.key, this.count, this.index, 1014).then((res) => {
        // console.log(res);
        // console.log(this.index);
        // this.videoList = [];
        this.hasMore = res.data.result.hasMore;
        const result = res.data.result.videos;
        // if (result.length < this.count + this.index) {
        this.pushData(result);
        // this.finished = true;
        // } else {
        //   this.pushData(result)
        // }
        // setTimeout(()=>{
        this.getUserAvatar(this.index);
        this.index += 5;
        // },1000)

        this.loading = false;
      });
    },
    pushData(result) {
      for (const item of result) {
        this.videoList.push({
          id: item.vid,
          title: item.title,
          coverUrl: item.coverUrl,
          uid: item.creator[0].userId,
          username: item.creator[0].userName,
        });
      }
    },
    onLoad() {
      if (++this.onLoadCount >= 9) {
        this.finished = true;
        return;
      }
      setTimeout(() => {
        this.getVideoList();
      }, 2000);
    },
    async getUserAvatar(i) {
      let index = 0;
      let newArray = this.videoList.slice(i)
      for (const item of newArray) {
        // console.log(item.uid);
        try {
          const { data: res } = await getUserDetail(item.uid);
          let result = res.profile.avatarUrl;
          this.$set(
            this.videoList[index+i], 
            "avatarUrl", 
            result
          );
        } catch (e) {
          // console.log(e);
          console.log("用户 " + item.username + " 头像请求失败，已启用默认头像");
          this.$set(
            this.videoList[index+i],
            "avatarUrl",
            "https://img0.baidu.com/it/u=797324285,1943411797&fm=26&fmt=auto&gp=0.jpg"
          );
        }
        //  console.log(res.avatarUrl);
        // console.log(this.videoList.slice(i));
        index++;
      }
      // console.log(this.videoList);
    },
  },
};
</script>

<style scoped>
.videoDetail {
  margin-top: 110px;
  margin-bottom: 50px;
}
.loading {
  margin-left: 40%;
}
.video-item {
  margin-bottom: 30px;
}
.user {
  display: flex;
  justify-content: left;
}
.user p {
  margin-left: 20px;
}
.user img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
}
.title p {
  min-width: 300px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.img img {
  width: 100%;
  height: 200px;
  border-radius: 10px;
}
</style>