<template>
  <div class="songList">
    <van-loading
      class="loading"
      text-color="#999"
      v-if="gedanList == ''"
      color="red"
      type="spinner"
      size="24px"
      >正在加载...</van-loading
    >
    <van-list
      :immediate-check="false"
      v-model="loading"
      v-if="gedanList != ''"
      :finished="finished"
      finished-text="查看更多请前往网易云APP~"
      @load="onLoad"
    >
      <div class="gedan">
        <div @click="toPlayListDetail(item.id)" v-for="(item,index) in gedanList" :key="index" class="gedan-item">
          <div class="img">
            <img :src="item.coverImgUrl+'?param=100y100'" />
          </div>
          <div class="gedan-text">
            <p class="name">{{ item.name }}</p>
            <p class="info">
              {{ item.trackCount }}首，by{{ item.creator }}，播放{{
                item.playCount
              }}次
            </p>
            <p class="clickPercentage" v-if="item.officialTags != ''">
              {{ item.officialTags }}
            </p>
          </div>
        </div>
      </div>
    </van-list>
  </div>
</template>

<script>
import { getSearchResult } from "../../../NetRequest/search";
import { toStringNum } from "../../../common/common.js";
export default {
  data() {
    return {
      gedanList: [],
      key: "",
      count: 15,
      loading: false,
      finished: false,
      index: 0,
      onLoadCount: 0,
      hasMore: true,
    };
  },
  mounted() {
    this.key = this.$route.query.key;
    setTimeout(() => {
      this.getGedanList();
    }, 1000);
  },
  methods: {
    getGedanList() {
      if (!this.hasMore) {
        this.finished = true;
        return;
      }
      getSearchResult(this.key, this.count , this.index, 1000).then(
        (res) => {
          // console.log(res);
          // this.gedanList = [];
          this.hasMore = res.data.result.hasMore;
          const result = res.data.result.playlists;
          // if (result.length < this.count + this.index) {
            // this.gedanList = [];
            this.pushData(result)
          //   this.finished = true;
          // } else {
          //   this.pushData(result)
            this.index += 10;
            // this.songList = [];
          // }
          this.loading = false;
        }
      );
    },
    pushData(result) {
      for (const item of result) {
        this.gedanList.push({
          id: item.id,
          name: item.name,
          trackCount: item.trackCount,
          creator: item.creator.nickname,
          playCount: toStringNum(item.playCount),
          officialTags: item.officialTags,
          coverImgUrl: item.coverImgUrl,
        });
      }
    },
    onLoad() {
      if (++this.onLoadCount >= 9) {
        this.finished = true;
        return;
      }
      setTimeout(() => {
        this.getGedanList();
      }, 2000);
    },
    /* 歌单详情 */
    toPlayListDetail(id) {
      // console.log(id);
      this.$router.push({
        path: "/recommendMore/playListDetail",
        query: {
          id,
        },
      });
    },
  },
};
</script>

<style scoped>
.songList {
  margin-top: 110px;
  margin-bottom: 50px;
}
p {
  height: 20px;
  margin: 5px;
  max-width: 240px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.loading {
  margin-top: 150px;
  margin-left: 40%;
}
.gedan-item {
  display: flex;
  justify-content: left;
  margin-bottom: 15px;
}
.gedan-item .img img {
  width: 60px;
  height: 60px;
  border-radius: 10px;
}
.gedan-text .info {
  font-size: 12px;
  color: rgb(149, 149, 149);
}
.clickPercentage {
  font-size: 12px;
  color: rgb(255, 200, 100);
}
</style>