<template>
  <div class="songContainer">
    <van-loading
      class="loading"
      text-color="#999"
      v-if="songList == ''"
      color="red"
      type="spinner"
      size="24px"
      >正在加载...</van-loading
    >
    <div v-if="songList != ''">
      <div class="header">
        <div class="playBtn">
          <div class="left">
            <div class="left-icon">
              <span class="iconfont icon-caret_right"></span>
            </div>
            <span>播放全部</span>
          </div>
          <span class="iconfont icon-icon-test"></span>
        </div>
      </div>
      <div class="content">
        <van-list
          :immediate-check="false"
          v-model="loading"
          :finished="finished"
          finished-text="查看更多请前往网易云APP~"
          @load="onLoad"
        >
          <div @click="playThis(item.id,index)" v-for="(item,index) in songList" :key="index" class="song">
            <div class="item">
              <div class="text">
                <p class="name">{{ item.name }}</p>
                <p class="singer">{{ item.artists }}-{{ item.album }}</p>
              </div>
              <div class="icon">
                <span class="iconfont icon-bofang"></span>
                <span class="iconfont icon-liebiao-dian"></span>
              </div>
            </div>
            <div class="divider"></div>
          </div>
        </van-list>
      </div>
    </div>
  </div>
</template>

<script>
import { getSearchResult } from "../../../NetRequest/search";
import { findItem } from "../../../common/common"
export default {
  data() {
    return {
      key: "",
      songList: [],
      count: 15,
      loading: false,
      finished: false,
      index: 0,
      onLoadCount: 0,
      hasMore: true,
      hasClcikMusicId:''
    };
  },
  mounted() {
    this.key = this.$route.query.key;
    // console.log(this.key);
    setTimeout(() => {
      this.getSongList();
    }, 1000);
  },
  methods: {
    getSongList() {
      if (!this.hasMore) {
        this.finished = true;
        return;
      }
      // console.log(key);
      getSearchResult(this.key, this.count , this.index, 1).then((res) => {
        console.log(res);
        this.hasMore = res.data.result.hasMore;
        const result = res.data.result.songs;
          // this.songList = [];
          this.pushData(result);
          this.index += 10;
          // this.songList = [];
        // }
        this.loading = false;
      });
      // console.log(this.index);
    },
    pushData(result) {
      for (const item of result) {
        this.songList.push({
          id: item.id,
          name: item.name,
          artists: item.artists[0].name,
          album: item.album.name,
          picUrl:item.album.artist.img1v1Url
        });
      }
    },
    onLoad() {
      // console.log("onLoad被触发了");
      // console.log(this.index);
      if (++this.onLoadCount >= 9) {
        this.finished = true;
        return;
      }
      setTimeout(() => {
        this.getSongList();
      }, 2000);
    },
    playThis(musicId,i){
      // console.log(musicId);
      this.$store.state.playShow = true
      const index = findItem(this.$store.state.playList,'id',musicId)
      if(index != -1){//如果播放列表有这首歌
        if(this.hasClcikMusicId == musicId){
          //如果被点击这首歌已经被点击过且正在播放
          return
        }
        this.$bus.$emit('receiveMusicId',musicId,index)
      } else{//播放列表没有这首歌
        const newIndex = this.$store.state.playList.length
        this.$store.state.playList.push(this.songList[i])
        // console.log(this.$store.state.playList);
        this.$bus.$emit('receiveMusicId',musicId,newIndex)
        this.hasClcikMusicId = musicId
      }
    },
  },
};
</script>

<style scoped>
.songContainer {
  margin-top: 110px;
  margin-bottom: 50px;
}
.loading {
  margin-top: 150px;
  margin-left: 40%;
}
.playBtn {
  padding: 10px 0;
  font-size: 14px;
  display: flex;
  margin-top: -2px;
  justify-content: space-between;
  margin-bottom: 15px;
  background-color: #fff;
}
.left {
  display: flex;
  justify-content: left;
}
.left-icon {
  width: 18px;
  height: 18px;
  line-height: 18px;
  text-align: center;
  margin-right: 10px;
  border-radius: 50%;
  background-color: red;
}
.icon-caret_right {
  font-size: 10px;
  color: #eee;
}
.icon-icon-test {
  font-size: 20px;
  margin-right: 5px;
}
.song .item {
  width: 100%;
  display: flex;
  justify-content: space-between;
  height: 60px;
}
p {
  height: 20px;
  margin: 5px;
  max-width: 220px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.singer {
  font-size: 13px;
  color: rgb(149, 149, 149);
}
.icon {
  line-height: 60px;
  text-align: center;
}
.icon .iconfont:nth-child(1) {
  margin-right: 15px;
}
.icon .iconfont {
  font-size: 20px;
  color: #999;
}
</style>