<template>
  <div class="contain">
    <div class="content">
      <div class="title">
        <span class="text">当前播放</span>
        <span class="count">({{ this.$store.state.playList.length }})</span>
      </div>
      <div class="header">
        <div class="left">
          <span :class="['iconfont', $store.state.loop ? 'icon-ziyuanldpi' : 'icon-xunhuan']"></span>
          <span class="text">{{$store.state.loop ?'单曲循环':'列表循环'}}</span>
        </div>
        <div class="right">
          <span class="iconfont icon-dingyue"></span>
          <span class="text">收藏全部</span>
          <span class="split-line">|</span>
          <span @click="clearList" class="iconfont icon-lajitong"></span>
        </div>
      </div>
      <div class="list">
        <div
          class="item"
          v-for="(item, index) in this.$store.state.playList"
          :key="item.id"
        >
          <div @click="playThis(item.id, index)" class="name">
            <p
              :class="[
                'song-title',
                $store.state.index == index ? 'activeColor' : '',
              ]"
            >
              {{ item.name }}&nbsp;-&nbsp;
            </p>
            <p
              :class="[
                'auther',
                $store.state.index == index ? 'activeColor' : '',
              ]"
            >
              {{ item.artists }}
            </p>
          </div>
          <span @click="deleteSong" v-show="$store.state.index == index ?false:true" class="el-icon-close"></span>
          <span class="activeColor" v-show="$store.state.index == index ?true:false">正在播放</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {};
  },
  methods: {
    playThis(musicId, index) {
      // this.$store.state.canPlay = true
      if (this.$store.state.index == index) return;
      this.$emit("send", musicId, index);
    },
    clearList() {
      if(this.$store.state.playList.length == 0){
        this.$toast('当前播放列表为空')
        return
      }
      this.$dialog.confirm({
        message: "确认清空播放列表？",
      })
        .then(() => {
          // on confirm
          this.$store.state.audio.pause()
          this.$store.state.isPlaying = false;
          this.$store.state.playList = []
          this.$store.state.loop = true
        })
        .catch(() => {
          // on cancel
        });
    },
    deleteSong(){
      this.$toast('功能暂未开发')
    }
  },
  mounted() {},
};
</script>

<style scoped>
.contain {
  position: absolute;
  width: 100%;
}
.content {
  padding: 15px;
}
.title .text {
  font-weight: 800;
}
.title .count {
  font-size: 12px;
  color: #999;
}
.header {
  margin-top: 15px;
  display: flex;
  justify-content: space-between;
}
.header .iconfont {
  line-height: 25px;
  text-align: center;
  /* margin-right: 10px; */
  /* font-size: 18px; */
  color: rgb(176, 176, 176);
}
.header .text {
  color: #666;
  font-size: 14px;
}
.icon-xunhuan,
.icon-dingyue,
.right .text,
.split-line {
  margin-right: 10px;
}
.split-line {
  color: rgb(176, 176, 176);
}
.item {
  margin-top: 15px;
  display: flex;
  justify-content: space-between;
}
.name {
  display: flex;
  justify-content: space-between;
}
.song-title {
  height: 20px;
  margin: 5px;
  max-width: 100px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.auther {
  line-height: 30px;
  text-align: center;
  margin-top: 2px;
  font-size: 12px;
  color: #999;
  max-width: 100px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.el-icon-close {
  line-height: 30px;
  text-align: center;
  font-size: 25px;
  color: #999;
}
.activeColor {
  color: red;
}
</style>