<template>
  <div class="contain">
    <div class="control" v-for="(item, index) in controlBarData" :key="index">
      <div class="left">
        <img
        :class="$store.state.isPlaying ? 'discAnimation' : ''"
        @click="toPlay"
        :src="item.picUrl+'?param=100y100'"
        alt=""
      />
      <p @click="toPlay" class="text"
        >{{ item.name }}-{{ item.artists }}</p
      >
      </div>
      <div class="right">
        <span
          @click="getPlayUrl(item.id)"
          :class="[
            'iconfont','bofang',
            !$store.state.isPlaying
              ? 'icon-bofang'
              : 'icon-ico_zanting_xuanzhong',
          ]"
        ></span>
        <span @click="showList" class="iconfont icon-liebiao"></span>
      </div>
      <van-popup
        v-model="$store.state.showPlayList"
        position="bottom"
        round
        :style="{ height: '60%', width: '100%' }"
        get-container="body"
      >
        <playList @send="receiveMusicId" />
      </van-popup>
      <!-- play -->
      <van-popup
        v-model="$store.state.playShow"
        position="bottom"
        :style="{ height: '100%', width: '100%' }"
        get-container="body"
      >
        <play @handlePlay="handlePlay" @changeSong="changeSong" />
      </van-popup>

      <audio
        id="audio"
        :src="
          'https://music.163.com/song/media/outer/url?id=' + playUrl + '.mp3'
        "
        @ended="playEnded"
        @timeupdate="currentTime"
        :loop="$store.state.loop?'loop':false"
      ></audio>
    </div>
  </div>
</template>

<script>
import { getNewSongs } from "../../../NetRequest/discover";

import play from "../play";
import playList from "./playList";
export default {
  components: {
    playList,
    play,
  },
  data() {
    return {
      newSong: [],//播放列表
      controlBarData: [],//控制条数据
      playUrl: "",//即将播放音乐的地址（id）
      audio:''
    };
  },
  async created() {
    // console.log(this);
    let localData = JSON.parse(localStorage.getItem("newSong"));
    if (localData) {
      // console.log("使用缓存数据");
      this.newSong = localData;
    } else {
      const res = await getNewSongs(0);
      // console.log(res);
      for (const item of res.data.data.slice(0, 30)) {
        this.newSong.push({
          id: item.id,
          name: item.name,
          artists: item.artists[0].name,
          picUrl: item.album.picUrl,
        });
      }
      localStorage.setItem("newSong", JSON.stringify(this.newSong));
    }
    this.controlBarData.splice(0, 1, this.newSong[0]);
    this.$store.state.playingMusicId = this.newSong[0].id;
    this.$store.state.playList = this.newSong
    
    // console.log(this.controlBarData);
    this.$store.state.isLoadMusic = true;
    this.$store.state.index = 0
    // console.log('改变了',this.playUrl);
  },
  mounted() {
    // console.log(this.$refs.sendMusicId.getMusicId)
    this.$store.state.audio = document.querySelector("#audio")
    this.$bus.$on('receiveMusicId',this.receiveMusicId)
    this.$bus.$on('handlePlay',this.handlePlay)
    // this.$bus.$on('getPlayUrl',this.getPlayUrl)
  },
  methods: {
    /* 跳转到播放页面 */
    toPlay() {
      // this.$router.push('/play');
      this.$store.state.playShow = true;
    },
    /* 展示播放列表 */
    showList() {
      if (this.$store.state.showPlayList) {
        this.$store.state.showPlayList = false;
      } else {
        this.$store.state.showPlayList = true;
      }
    },
    /* 点击播放按钮获取音乐id */
    getPlayUrl(musicId) {
      if (!this.playUrl) {
        this.playUrl = musicId;
        this.$store.state.playingMusicId = this.playUrl;
        // console.log('改变了',this.playUrl);
      }
      // this.$store.state.canPlay = true
      this.handlePlay();
    },
    /* 播放与暂停 */
    async handlePlay() {
      // console.log('111',audio.readyState)
      if (this.$store.state.isLoadMusic) {
        await this.$store.state.audio.load();
        // console.log('222',audio.readyState)
        this.$store.state.isLoadMusic = false;
      }
      
      if (!this.$store.state.isPlaying) {
        try{
          await this.$store.state.audio.play();
          }catch(err){
            this.$toast("抱歉！该歌曲暂不支持播放，为您自动切换到下一首！");
            setTimeout(()=>{
              this.playEnded()
              },3000);
            return;
          }
        
        // console.log('333',audio.readyState)
        this.$store.state.isPlaying = true;
        // this.$store.state.canPlay = true
      } else {
        this.$store.state.audio.pause();
        this.$store.state.isPlaying = false;
        // this.$store.state.canPlay = true
      }
    },
    /* 播放结束 */
    playEnded() {
      // console.log("播放结束");
      this.$bus.$emit("playEnd", {
        playEnd: true,
      });
      this.$store.state.currentTime = 0
      this.$store.state.duration = 0
      this.$store.state.isPlaying = false;
      this.$store.state.isLoadMusic = true;
      // this.$store.state.canPlay = true
      if (this.$store.state.index == this.$store.state.playList.length - 1) {
        this.$store.state.index = 0;
      } else {
        this.$store.state.index++;
      }
      /* 解决刷新页面后切换到播放列表第一首歌 */
      // sessionStorage.setItem('listIndex',this.$store.state.index)

      this.controlBarData.splice(0, 1, this.$store.state.playList[this.$store.state.index]);

      this.playUrl = this.controlBarData[0].id;
      this.$store.state.playingMusicId = this.playUrl;
      // console.log(this.$store.state.isLoadMusic);
      // console.log(this.playUrl);
      setTimeout(() => {
        this.handlePlay();
      }, 150);
    },
    /* 接收播放列表传来的音乐id */
    receiveMusicId(id, i) {
      // console.log('receiveMusicId被触发了',id,i);
      this.$store.state.index = i;
      this.$store.state.isPlaying = false;
      this.$store.state.isLoadMusic = true;
      this.controlBarData.splice(0, 1, this.$store.state.playList[this.$store.state.index]);

      this.playUrl = id;
      this.$store.state.playingMusicId = this.playUrl;
      // console.log('改变了',this.playUrl);
      setTimeout(() => {
        this.handlePlay();
      }, 150);
    },
    /* 音乐实时播放进度 */
    currentTime() {
      // console.log(this.$store.state.audio.duration,this.$store.state.audio.currentTime);
      if(!this.$store.state.isTimeUpData)
        return
      this.$store.state.duration = this.$store.state.audio.duration
      this.$store.state.currentTime = this.$store.state.audio.currentTime
    },
    /* 切换歌曲 */
    changeSong(type) {
      if (type === "next") {
        this.playEnded();
      } else {
        if (this.$store.state.index === 0) {
          this.$store.state.index = this.$store.state.playList.length;
        }
        this.$store.state.index--
        // console.log(this.$store.state.index);
        this.$store.state.isPlaying = false;
        this.$store.state.isLoadMusic = true;
        // this.canPlay = true
        this.controlBarData.splice(0, 1, this.$store.state.playList[this.$store.state.index]);

        this.playUrl = this.controlBarData[0].id;
        this.$store.state.playingMusicId = this.playUrl;
        setTimeout(() => {
          this.handlePlay();
        }, 150);
      }
    },
  },
  beforeDestroy() {
    this.$bus.$off("receiveMusicId","handlePlay")
  },
};
</script>

<style scoped>
.contain {
  width: 100%;
}
.control {
  display: flex;
  justify-content: space-between;
  width: 100%;
  padding: 0 15px;
  height: 50px;
  background-color: white;
}
.control img {
  margin-top: 2.5px;
  width: 45px;
  height: 45px;
  border-radius: 50%;
}
.left{
  display: flex;
  justify-content: left;
  line-height: 50px;
  text-align: center;
}
p {
  height: 50px;
  margin: 0px 10px;
  max-width: 200px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.right{
  display: flex;
  justify-content: space-around;
}
.icon-liebiao{
  margin-right: 25px;
}
.bofang{
  margin-right: 15px;
}
.control .iconfont {
  line-height: 50px;
  text-align: center;
  font-size: 30px;
}
.discAnimation {
  animation: discRotate 4s linear infinite;
  animation-delay: 1s;
}
@keyframes discRotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
</style>