<template>
  <div class="contain">
    <div v-if="$store.state.musicInfo.length" class="bg">
      <img :src="$store.state.musicInfo[0].picUrl + '?params=500y500'" alt="" />
    </div>
    <div v-if="$store.state.musicInfo.length" class="play">
      <div class="head">
        <i @click="backIndex" class="iconfont icon-xiangxia"></i>
        <div class="author">
          <p>{{ $store.state.musicInfo[0].name }}</p>
          <p>{{ $store.state.musicInfo[0].artists }}</p>
        </div>
        <i class="iconfont icon-fenxiang"></i>
      </div>
      <transition name="logo" mode="out-in">
        <div
          v-show="!$store.state.isShowLyric"
          @click="$store.state.isShowLyric = true"
          class="content"
        >
          <div class="circle"></div>
          <img
            :class="['needle', { needleRotate: $store.state.isPlaying }]"
            src="../../assets/image/needle.png"
            alt=""
          />
          <div
            :class="[
              'discContainer',
              { discAnimation: $store.state.isPlaying },
            ]"
          >
            <img class="disc" src="../../assets/image/disc.png" alt="" />
            <img
              class="musicImg"
              :src="$store.state.musicInfo[0].picUrl + '?params=300y300'"
              alt=""
            />
          </div>
        </div>
      </transition>
      <transition name="lyric" mode="out-in">
        <lyric class="lyric" v-show="$store.state.isShowLyric" />
      </transition>
      <div class="footer">
        <div class="behavior">
          <i class="iconfont icon-xin"></i>
          <i class="iconfont icon-download"></i>
          <i class="iconfont icon-gechang"></i>
          <i @click="toComment" class="iconfont icon-pinglun"></i>
          <i class="iconfont icon-liebiao-dian"></i>
        </div>
        <div class="progress">
          <span>{{ currentTime }}</span>
          <div class="bar">
            <!-- <div class="currentBar" :style="{ width: currentBarWidth + 'px' }"> -->
            <!-- <div class="aduio-circle"></div>
            </div> -->
            <van-slider @change="onChange" active-color="#333" v-model="value">
              <template #button>
                <div
                  @touchstart="$store.state.isTimeUpData = false"
                  @touchend="$store.state.isTimeUpData = true"
                  class="circle2"
                ></div>
              </template>
            </van-slider>
          </div>
          <span>{{ duration }}</span>
        </div>
        <div class="control">
          <i
            @click="handleSwitch"
            :class="['iconfont', $store.state.loop ? 'icon-ziyuanldpi' : 'icon-xunhuan']"
          ></i>
          <i
            @click="changeSong('pre')"
            class="iconfont icon-shangyishoushangyige"
          ></i>
          <i
            @click="play"
            :class="[
              'iconfont',
              !$store.state.isPlaying
                ? 'icon-bofang'
                : 'icon-ico_zanting_xuanzhong',
            ]"
          ></i>
          <i
            @click="changeSong('next')"
            class="iconfont icon-xiayigexiayishou"
          ></i>
          <i @click="showPlayList" class="iconfont icon-liebiao"></i>
        </div>
      </div>
    </div>
    <!-- <div v-show="$store.state.showComment" class="comment"> -->
    <van-popup
      v-model="$store.state.showComment"
      position="bottom"
      :style="{ height: '100%' }"
    >
      <comment ref="sendComments" />
    </van-popup>
    <!-- </div> -->
  </div>
</template>

<script>
import comment from "./childrenComps/comment";
import lyric from "./childrenComps/lyric";
import { getMusicDetail } from "../../NetRequest/play";
export default {
  components: {
    comment,
    lyric,
  },
  data() {
    return {
      index: 0,
      value: 0,
      isShowLyric: false,
      showComment: false,
    };
  },
  created() {},
  mounted() {
    this.myGetMusicDetail();
    setTimeout(() => {
      this.showComment = true;
    }, 2000);
  },
  methods: {
    /* 返回首页 */
    backIndex() {
      this.$store.state.playShow = false;
    },
    /* 控制音乐播放/暂停 */
    play() {
      this.$emit("handlePlay");
    },
    /* 切换播放模式 */
    handleSwitch() {
      if (!this.$store.state.loop) {
        this.$store.state.loop = true;
        this.$toast('循环播放')
      } else {
        this.$store.state.loop = false;
        this.$toast('顺序播放')
      }
    },
    /* 跳转到歌曲评论页面 */
    toComment() {
      this.$store.state.showComment = true;
    },
    /* 获取音乐详情 */
    async myGetMusicDetail() {
      const res = await getMusicDetail(this.musicId);
      // console.log(res);
      // for (const item of res.data.songs) {
      this.$store.state.musicInfo = res.data.songs.map((item) => ({
        id: item.al.id,
        name: item.name,
        artists: item.ar[0].name,
        picUrl: item.al.picUrl,
      }));
      // }
      // console.log(this.musicInfo);
    },
    /* 切歌 */
    changeSong(type) {
      // console.log(type);
      // this.$store.state.canPlay = true
      this.$emit("changeSong", type);
    },
    /* 打开播放列表 */
    showPlayList() {
      this.$store.state.showPlayList = true;
    },
    /* 格式化音频时长 */
    formatTime(time) {
      let min = (Math.floor(time / 60) + "").padStart(2, "0"); // 分钟
      let second = (Math.floor(time - 60 * min) + "").padStart(2, "0"); // 秒数
      return min + ":" + second;
    },
    /* 调整播放进度条 */
    onChange(value) {
      // console.log(value);
      this.$store.state.audio.currentTime =
        (value / 100) * this.$store.state.duration;
    },
  },

  computed: {
    musicId() {
      return this.$store.state.playingMusicId;
    },
    currentTime() {
      return this.formatTime(this.$store.state.currentTime);
    },
    duration() {
      return this.formatTime(this.$store.state.duration);
    },
    currentBarWidth() {
      return (this.$store.state.currentTime / this.$store.state.duration) * 100;
    },
  },
  watch: {
    musicId() {
      // console.log("musicId改变了");
      this.myGetMusicDetail();
      if(!this.$store.state.updataComment)
        return
        this.$refs.sendComments.updata(this.musicId);
    },
    immediate: true,
    currentBarWidth() {
      this.value = this.currentBarWidth;
    },
  },
};
</script>

<style scoped>
.contain {
  height: 100%;
}
.bg img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  -webkit-filter: blur(15px);
  -moz-filter: blur(15px);
  -o-filter: blur(15px);
  -ms-filter: blur(15px);
  filter: blur(15px);
}
.play {
  /* background-color: aqua; */
  position: relative;
  padding: 0px 15px;
  height: 100%;
  overflow: hidden;
}
.head {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
  line-height: 60px;
  text-align: center;
}
.author {
  width: 150px;
  height: 60px;
}
.author p {
  margin: 0px;
  padding: 0px;
  line-height: 30px;
  text-align: center;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.author p:nth-child(1) {
  color: #eee;
}
.author p:nth-child(2) {
  color: #bbb;
  font-size: 13px;
}
.head .iconfont {
  font-size: 20px;
  color: #eee;
}
.content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.circle {
  position: relative;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-color: #fff;
  z-index: 100;
}
.needle {
  width: 96px;
  height: 137px;
  position: relative;
  left: 30px;
  top: -20px;
  z-index: 99;
  transform-origin: 20px 0; /* 旋转中心点，默认为元素中心点 */
  transform: rotate(-20deg);
  transition: transform 1s;
}
.needleRotate {
  transform: rotate(0deg);
}
.discContainer {
  width: 296px;
  height: 296px;
  position: relative;
  top: -85px;
}
.discAnimation {
  animation: discRotate 4s linear infinite;
  animation-delay: 1s;
}
@keyframes discRotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.disc {
  width: 296px;
  height: 296px;
}
.musicImg {
  width: 185px;
  height: 185px;
  border-radius: 50%;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
}
.footer {
  position: absolute;
  bottom: 30px;
  width: 90%;
}
.control,
.behavior {
  height: 30px;
  display: flex;
  justify-content: space-around;
}
.control .iconfont,
.behavior .iconfont {
  font-size: 20px;
  line-height: 30px;
  text-align: center;
  color: #eee;
}
.control .iconfont:nth-child(3) {
  font-size: 40px;
}
.progress {
  line-height: 50px;
  text-align: center;
  display: flex;
  justify-content: space-around;
  font-size: 10px;
  color: #eee;
}
.bar {
  position: relative;
  width: 240px;
  height: 1px;
  background-color: #eee;
  margin: auto;
}
.currentBar {
  position: absolute;
  top: 0;
  left: 0;
  height: 1px;
  background-color: #666;
  z-index: 9;
}
.aduio-circle {
  position: absolute;
  top: -3px;
  right: -8px;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #fff;
}
.circle2 {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #fff;
}
.lyric {
  height: 70%;
}
.v-enter {
  opacity: 0;
  /* 进来的时候从右 */
  transform: translateX(100%);
}
.v-leave-to {
  opacity: 0;
  /* 离开的时候向左 */
  transform: translateX(-100%);
  position: absolute;
}

/* 动画执行期间 */
.v-enter-active,
.v-leave-active {
  position: absolute;
  /* 添加动画效果 */
  transition: all 0.2s linear;
}
.logo-enter {
  opacity: 1;
  position: absolute;
  z-index: -10;
  transform: translateX(-350px);
}
.logo-leave-to {
  opacity: 1;
  position: absolute;
  z-index: -10;
  transform: translateX(-350px);
}

.logo-enter-active,
.logo-leave-active {
  transition: 0.8s all ease-in-out;
}

.lyric-enter {
  position: absolute;
  z-index: -10;
  opacity: 1;
  transform: translateX(350px);
}
.lyric-leave-to {
  position: absolute;
  top: 42.5px;
  z-index: 1;
  opacity: 1;
  transform: translateX(700px);
}

.lyric-enter-active,
.lyric-leave-active {
  transition: 0.8s all ease-in-out;
}
/* .comment{
  z-index: 999;
} */
</style>