<template>
  <div class="user">
    <van-loading
      class="loading"
      text-color="#999"
      v-if="userList == ''"
      color="red"
      type="spinner"
      size="24px"
      >正在加载...</van-loading
    >
    <van-list
      v-if="userList != ''"
      :immediate-check="false"
      v-model="loading"
      :finished="finished"
      finished-text="查看更多请前往网易云APP~"
      @load="onLoad"
    >
      <div class="user">
        <div v-for="(item,index) in userList" :key="index" class="user-item">
          <div class="right">
            <div class="img">
              <img :src="item.picUrl+'?param=100y100'" />
            </div>
            <div class="user-text">
              <p class="name">
                {{ item.name }}&nbsp;<span
                  v-if="item.gender"
                  :class="[
                    'iconfont',
                    item.gender === 1 ? 'icon-nan' : 'icon-nv',
                  ]"
                ></span>
              </p>
              <p class="info">{{ item.signature }}</p>
            </div>
          </div>
          <div class="focus-btn">
            <span>+ 关注</span>
          </div>
        </div>
      </div>
    </van-list>
  </div>
</template>

<script>
import { getSearchResult } from "../../../NetRequest/search";
export default {
  data() {
    return {
      userList: [],
      key: "",
      count: 15,
      loading: false,
      finished: false,
      index: 0,
      onLoadCount: 0,
      hasMore: true,
    };
  },
  mounted() {
    this.key = this.$route.query.key;
    setTimeout(() => {
      this.getUserList();
    }, 1000);
  },
  methods: {
    getUserList() {
      if (!this.hasMore) {
        this.finished = true;
        return;
      }
      getSearchResult(this.key, this.count , this.index, 1002).then(
        (res) => {
          // console.log(res);
          // this.userList = [];
        this.hasMore = res.data.result.hasMore;
          const result = res.data.result.userprofiles;
          // if (result.length < this.count + this.index) {
            this.pushData(result)
          //   this.finished = true;
          // } else {
          //   this.pushData(result)
            this.index += 10;
          // }
          this.loading = false;
        }
      );
    },
    pushData(result) {
      for (const item of result) {
        this.userList.push({
          id: item.userId,
          name: item.nickname,
          picUrl: item.avatarUrl,
          signature: item.signature,
          gender: item.gender,
        });
      }
    },
    onLoad() {
      if (++this.onLoadCount >= 9) {
        this.finished = true;
        return;
      }
      setTimeout(() => {
        this.getUserList();
      }, 2000);
    },
  },
};
</script>

<style scoped>
.user {
  margin-top: 110px;
  margin-bottom: 50px;
}
p {
  height: 20px;
  margin: 5px;
  max-width: 150px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.loading {
  margin-top: 150px;
  margin-left: 40%;
}
.user-item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}
.right {
  display: flex;
  justify-content: left;
}
.img {
  display: flex;
  justify-content: left;
}
.user-item .img img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  margin-right: 20px;
}
.focus-btn {
  width: 65px;
  height: 25px;
  line-height: 25px;
  text-align: center;
  border: red 1px solid;
  color: red;
  border-radius: 20px;
  margin: 15px;
}
.user-text .info {
  font-size: 10px;
  color: rgb(149, 149, 149);
}
.icon-nan {
  color: rgb(116, 200, 249);
}
.icon-nv {
  color: rgb(240, 141, 184);
}
</style>