import Vue from 'vue'
import VueRouter from 'vue-router'
import songDetail from '@/views/search/childrenCopms/songDetail'
import albumDetail from '@/views/search/childrenCopms/albumDetail'
import videoDetail from '@/views/search/childrenCopms/videoDetail'
import singerDetail from '@/views/search/childrenCopms/singerDetail'
import songListDetail from '@/views/search/childrenCopms/songListDetail'
import userList from '@/views/search/childrenCopms/userList'
import comprehensive from '@/views/search/childrenCopms/comprehensive'

Vue.use(VueRouter)

/* 解决路由跳转报错问题 */
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch(err => err)
}

const routes = [
  {
    path: '/',
    name: 'tabbar',
    component: () => import('@/views/tabbar'),
    redirect: '/discover',
    children: [
      {
        path: 'discover',
        component: () => import('@/views/discover/discover')
      },
      {
        path: 'myMusic',
        component: () => import('@/views/my/myMusic')
      },
      {
        path: 'video',
        component: () => import('@/views/video/video')
      },
    ]
  },
  {
    path: '/search',
    component: () => import('@/views/search/search'),
    children: [
      {
        path: '/search/songDetail',
        component: songDetail
      },
      {
        path: '/search/albumDetail',
        component: albumDetail
      },
      {
        path: '/search/videoDetail',
        component: videoDetail
      },
      {
        path: '/search/singerDetail',
        component: singerDetail
      },
      {
        path: '/search/songListDetail',
        component: songListDetail
      },
      {
        path: '/search/userList',
        component: userList
      }, {
        path: '/search/comprehensive',
        component: comprehensive
      },
    ]
  },
  {
    path: '/discover/newSongsMore',
    component: () => import('@/components/newSongsMore/newSongsDetail')
  },
  {
    path: '/discover/recommendMore',
    component: () => import('@/components/recommendMore/recommendDetail')
  },
  {
    path: '/recommendMore/playListDetail',
    component: () => import('@/components/recommendMore/playListDetail')
  },
  {
    path: '/discover/MvTopMore',
    component: () => import('@/components/MvTopMore/MvDetail')
  },
  {
    path: '/discover/singerTopMore',
    component: () => import('@/components/singerTopMore/singerTop')
  },
  {
    path: '/discover/singerTopMore/singerDetailMore',
    component: () => import('@/components/singerTopMore/singerDetailMore')
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
