<template>
  <div class="LyricList">
    <div @click="hiddrenLyric" class="LyricListContent">
      <ul ref="list" v-if="lyricText.length !== 0">
        <li class="item" v-for="(item, index) in lyricText" :key="index">
          <!-- v-if="item !== '' && item !== '\n'" -->
          <div :style="{color:index === activeIndex?color:'#999'}"
          class="itemSpan">
            {{ item }}
          </div>
        </li>
      </ul>
    </div>
    <div 
     v-show="!sgc"
     class="block">
    <el-color-picker 
     size="mini"
     color-format="rgb"
     v-model="color">
    </el-color-picker>
    </div>
  </div>
</template>

<script>
import { getLyric } from "../../../NetRequest/play";
export default {
  name: "LyricList",
  data() {
    return {
      activeIndex: 0,
      index: 0,
      yscroll: 0,
      songTime: 0, // 歌曲实时时间
      num: 0,
      lyricText: "",
      songLyric: "",
      sgc:true,
      color:'#eee'
    };
  },
  methods: {
    getLyricData() {
      getLyric(this.$store.state.playingMusicId).then((res) => {
        //   console.log(res);
        this.sgc = res.data.sgc
        if (!this.sgc) {
          let text = res.data.lrc.lyric;
          let reg = /[[|[0-9:.]|]]/gi; // 正则匹配去除 []
          let lyric = text.replace(reg, "");
          this.lyricText = lyric.split("]"); // 将字符串转为数组
          var reg2 = /\[(.+?)\]/g; // 正则匹配出时间
          let time = text.match(reg2); // match 选中匹配成功的内容
        
          for (let i = 0; i < time.length; i++) {
            time[i] = time[i].slice(1, 9); // slice 截取
            time[i] =
              parseFloat(time[i].slice(0, 2), 2) * 60 +
              parseFloat(time[i].slice(3, 8), 2); // 截取分秒计算秒数
          }

          this.songLyric = time;
          let num = 0;
          // for循环遍历歌词数组
          for (let i = 0; i < this.songLyric.length; i++) {
            // 检查是否有 NaN 设置为 0
            if (isNaN(this.songLyric[i])) {
              this.songLyric.splice(i, 1, (num += 0.15));
            }
            if (
              this.songLyric[i] == this.songLyric[i + 1] ||
              this.songLyric[i] >= this.songLyric[i + 1]
            ) {
              this.songLyric.splice(i + 1, 1, this.songLyric[i + 1] + 0.5);
            }
          }
        }else{
            this.lyricText = ['暂无歌词']
        }
      });
    },
    timeUpDate() {
      let scrollHeight = 0;
      let scrollViewHeight =
        document.getElementsByClassName("LyricListContent")[0].clientHeight / 2;

      // 判断有没有歌词
      if (this.lyricText.length != 1) {
        // 判断歌曲时间在歌词区间
        if (
          this.$store.state.audio.currentTime >= this.songLyric[this.index] &&
          this.$store.state.audio.currentTime <= this.songLyric[this.index + 1]
        ) {
          let itemToTop = document.getElementsByClassName("itemSpan")[
            this.index
          ].offsetTop;
          let itemClientHeight = document.getElementsByClassName("itemSpan")[
            this.index
          ].clientHeight; // 歌词item高度的一半
          if (itemToTop > scrollViewHeight) {
            scrollHeight = itemToTop - scrollViewHeight + itemClientHeight; // 计算滚动位置
            this.$refs.list.style.transform =
              "translateY(" + -scrollHeight + "px)";
          }
          this.index++; // 索引++
          this.activeIndex++; // 歌词索引++
        }
      }
    },
    hiddrenLyric() {
      this.$store.state.isShowLyric = false;
    },
    scrollLyric() {
    //   this.$nextTick(() => {
        let scrollHeight = 0;
        this.$bus.$on("playEnd", ({ playEnd }) => {
          if (playEnd) {
            scrollHeight = 0; // 滚动回顶部
            this.$refs.list.style.transform =
              "translateY(" + scrollHeight + "px)"; // 设置滚动位置
          }
        });
        // 监听歌曲播放
        this.$store.state.audio.addEventListener("timeupdate", this.timeUpDate);

        // 添加 seeked 事件，当拖拽进度的时候触发
        this.$store.state.audio.addEventListener("seeked", () => {
            if (this.lyricText.length == 1)
             return
          let scrollViewHeight =
            document.getElementsByClassName("LyricListContent")[0]
              .clientHeight / 2;
          let seekedIndex = this.songLyric.filter((item) => {
            // 拖拽进度条后所在的歌词索引
            return this.$store.state.audio.currentTime >= item;
          }).length;
          let itemToTop = document.getElementsByClassName("itemSpan")[
            seekedIndex
          ].offsetTop; // 歌词item距离顶部的距离
          let itemClientHeight = document.getElementsByClassName("itemSpan")[
            seekedIndex
          ].clientHeight; // 歌词item高度的一半
          if (itemToTop > scrollViewHeight) {
            // 判断是否需要进行滚动
            scrollHeight = itemToTop - scrollViewHeight + itemClientHeight; // 计算滚动位置
            this.$refs.list.style.transform =
              "translateY(" + -scrollHeight + "px)";
          }
          this.index = seekedIndex; // 修改索引
          this.activeIndex = seekedIndex; // 修改索引
        });
    //   });
    },
  },
  created() {
    // 获取歌词
    this.getLyricData();
  },
  mounted() {
    setTimeout(()=>{
        this.scrollLyric();
    },2000)
  },
  computed: {
    // linearSet() {
    //   return this.$store.state.playSong.linearIndex;
    // },
    musicId() {
      return this.$store.state.playingMusicId;
    },
  },
  watch: {
    musicId() {
      // console.log("musicId改变了");
      this.index = 0
      this.activeIndex = 0
      this.getLyricData();
      this.scrollLyric()
    },
  },
  destroyed() {
    this.$store.state.navMusicDom.removeEventListener(
      "timeupdate",
      this.timeUpDate
    );
  },
};
</script>
<style scoped>
.noItem {
  float: left;
  width: 100%;
  height: 0.553928rem;
}
.playbox {
  width: 100%;
}
.list {
  transition: all 0.5s linear;
}
.LyricListContent {
  width: 80%;
  position: relative;
  top: 3%;
  margin: 0 auto;
  height: 90%;
  text-align: center;
  overflow: hidden;
  font-size: 0.346667rem;
  letter-spacing: 0.053333rem;
  color: #757575;
}
.LyricListContent ul {
  transition: 0.3s linear;
}
.active {
  color: #fff;
}
.item {
  margin-top: 17px;
}
.nolyric {
  position: relative;
  font-size: 20px;
  color: #fff;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.block{
    float: right;
}
</style>