import Vue from 'vue'
import App from './App.vue'
import router from './router'
import Vant from 'vant';
import 'vant/lib/index.css';
import '@/assets/css/iconfont.css'
// import '@/assets/css/setIcon.css'
// import '@/assets/css/setIcon.css'
import '@/assets/css/music.css'
import store from './store'
// import '../src/assets/utils/rem'

import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);
// import '@/assets/mui/css/mui.css'
// import "@/assets/mui/js/mui.min.js";

Vue.use(Vant)
Vue.config.productionTip = false

import moment from 'moment' 
// 过滤器  pattern 时间格式
Vue.filter('getTime',function(date,pattern="YYYY.MM.DD"){
  // 调用 moment 方法传入时间 获取指定的时间
  // 调用 format 格式化时间
  return moment(date).format(pattern)
})

Vue.filter('musicTime', function(time){
  let min = (Math.floor(time / 60) + '').padStart(2, '0');  // 分钟
  let second = (time - (60 * min) + '').padStart(2, '0');  // 秒数
  return min+':'+second
})

new Vue({
  router,
  store,
  beforeCreate(){
    Vue.prototype.$bus = this
  },
  render: h => h(App)
}).$mount('#app')
