import { request } from "./request";

//音乐播放地址
export function getMusicUrl(id){
    return request ({
        url: '/song/url',
        params: {
            id
        }
    })
}

//音乐详情
export function getMusicDetail(ids){
    return request ({
        url: '/song/detail',
        params: {
            ids
        }
    })
}

//歌曲评论
export function getMusicComments(id,limit,offset){
    return request ({
        url: '/comment/music',
        params: {
            id,
            limit,
            offset
        }
    })
}

// 获取歌词
export function getLyric(id){
    return request ({
        url: '/lyric',
        params: {
            id,
        }
    })
}