<template>
  <div class="singer">
    <van-loading
      class="loading"
      text-color="#999"
      v-if="singerList == ''"
      color="red"
      type="spinner"
      size="24px"
      >正在加载...</van-loading
    >
    <van-list
      :immediate-check="false"
      v-if="singerList != ''"
      v-model="loading"
      :finished="finished"
      finished-text="查看更多请前往网易云APP~"
      @load="onLoad"
    >
      <div class="gedan">
        <div v-for="(item,index) in singerList" :key="index" class="singer-item">
          <div class="img">
            <img :src="item.picUrl+'?param=100y100'" />
            <p class="name">{{ item.name }}</p>
          </div>
          <div class="focus-btn">
            <span>+ 关注</span>
          </div>
        </div>
      </div>
    </van-list>
  </div>
</template>

<script>
import { getSearchResult } from "../../../NetRequest/search";
export default {
  data() {
    return {
      singerList: [],
      key: "",
      count: 15,
      loading: false,
      finished: false,
      index: 0,
      onLoadCount: 0,
      hasMore: true,
    };
  },
  mounted() {
    this.key = this.$route.query.key;
    setTimeout(() => {
      this.getSingerList();
    }, 1000);
  },
  methods: {
    getSingerList() {
      if (!this.hasMore) {
        this.finished = true;
        return;
      }
      getSearchResult(this.key, this.count , this.index, 100).then(
        (res) => {
          console.log(res);
          // this.singerList = [];
        this.hasMore = res.data.result.hasMore;
          const result = res.data.result.artists;
          // if (result.length < this.count + this.index) {
            this.pushData(result);
          //   this.finished = true;
          // } else {
          //   this.pushData(result);
            this.index += 10;
          // }
          this.loading = false;
        }
      );
    },
    pushData(result) {
      for (const item of result) {
        this.singerList.push({
          id: item.id,
          name: item.name,
          picUrl: item.picUrl,
        });
      }
    },
    onLoad() {
      if (++this.onLoadCount >= 9) {
        this.finished = true;
        return;
      }
      setTimeout(() => {
        this.getSingerList();
      }, 2000);
    },
  },
};
</script>

<style scoped>
.singer {
  margin-top: 110px;
  margin-bottom: 50px;
}
.singer-item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}
.loading {
  margin-top: 150px;
  margin-left: 40%;
}
.img {
  display: flex;
  justify-content: left;
}
.singer-item .img img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  margin-right: 20px;
}
.focus-btn {
  width: 65px;
  height: 25px;
  line-height: 25px;
  text-align: center;
  border: red 1px solid;
  color: red;
  border-radius: 20px;
  margin: 15px;
}
.name{
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 150px;
  margin: 0;
}
</style>