import { request } from "./request";

//默认搜索关键字
export function getDefaultKey(){
    return request({
        url: '/search/default'
    })
}

//热搜榜
export function getHotSearch(){
    return request({
        url: '/search/hot/detail'
    })
}

//搜索
export function getSearchResult(keywords, limit, offset, type){
    return request({
        url: '/search',
        params: {
            keywords,
            limit,
            offset, 
            type
        }
    })
}