    showComment:false,//控制评论页面显示与隐藏

    isPlaying:false,//音乐播放与暂停

    isLoadMusic:true,//是否重新加载音乐

    currentTime:0,//音乐实时播放时间

    duration:0,//音乐总时长

    index:0,//播放列表索引

    showPlayList:false,//控制播放列表页面显示与隐藏

    playingMusicId:null,//当前播放音乐id

    playList:[],//播放列表

    controlBarStyle:false,//控制controlBar离开tabbar触底

    showControlBar:true,//控制controlBar显示与隐藏

    showPlayMv:false,//控制showPlayMv显示与隐藏

    palyMvInfo:[],//播放Mv信息

    isReGetMvInfo:false,//重新获取mv信息

    singerId:'',//歌手id

    audio:'',//音频标签

    isTimeUpData:true,//更新播放时间

    isShowLyric:false,//是否显示歌词

    musicInfo:{},//当前播放音乐基本信息

    updataComment:false,//是否更新评论

